import React, { useState, useEffect } from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import Card from "@material-ui/core/Card";
import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import clsx from "clsx";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Snackbar from "@material-ui/core/Snackbar";
import AboutUs from "../aboutus/AboutUs";

import history from "./../../history";
import ReactMarkdown from "react-markdown";

import { baseURL } from "./../../apis/util";

import theme from "./../ui/Theme";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: "100%",
    //height: "100%",
    //height: 350,
    width: "100%",

    marginLeft: "10px",
    //borderRadius: 30,
    marginTop: "2em",
    marginBottom: "1em",
    padding: 0,
    // "&:hover": {
    //   //border: "solid",
    //   //borderColor: theme.palette.common.grey,
    // },
  },
  rootMobile: {
    maxWidth: "100%",
    //height: 440,
    //height: "100%",
    width: "100%",

    marginLeft: "0px",
    //borderRadius: 30,
    marginTop: "2.5em",
    marginBottom: "0.5em",
    padding: 0,
    backgroundColor: "#FFFFFF",

    "&:hover": {
      //border: "solid",
      //borderColor: theme.palette.common.grey,
    },
  },
  mediaMobile: {
    height: "100%",
    width: "100%",
    // marginLeft: "80px",
  },
  media: {
    height: "100%",
    width: "100%",
    //marginLeft: "80px",
    //marginTop: "80px",
  },

  learnButton: {
    ...theme.typography.learnButton,
    fontSize: "0.7rem",
    height: 35,
    padding: 5,
    marginTop: "55px",
    marginLeft: "160px",
    border: `2px solid ${theme.palette.common.blue}`,
    [theme.breakpoints.down("sm")]: {
      marginBottom: "2em",
    },
  },
  dialog: {
    //maxWidth: 325,
    maxWidth: 500,
    //height: 450,
    marginLeft: "10px",
    borderRadius: 30,
    //marginTop: "10em",
    padding: 0,
    marginTop: -20,
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "250px",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  submitButton: {
    borderRadius: 10,
    height: 40,
    width: 160,
    marginLeft: "30rem",
    marginTop: 30,
    color: "white",
    backgroundColor: theme.palette.common.green,
    "&:hover": {
      backgroundColor: theme.palette.common.orange,
      color: "white",
    },
  },
}));

export default function WhoWeAreHeader(props) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [openLoginForm, setOpenLoginForm] = useState(false);
  const [openSignUpForm, setOpenSignUpForm] = useState(false);
  const [openForgotPasswordForm, setOpenForgotPasswordForm] = useState(false);

  const [currencyName, setCurrencyName] = useState("naira");
  const [countryName, setCountryName] = useState();
  const [stateName, setStateName] = useState();
  const [product, setProduct] = useState({});
  const [vendorName, setVendorName] = useState();
  const [minLearnerSlot, setMinLearnerSlot] = useState(1);
  const [loading, setLoading] = useState();
  const [addEvent, setAddEvent] = useState(false);
  const [updateEvent, setUpdateEvent] = useState(false);

  const handleSuccessfulCreateSnackbar = (message) => {
    //setBecomePartnerOpen(false);
    setAlert({
      open: true,
      message: message,
      //backgroundColor: "#4BB543",
      backgroundColor: "#FF731D",
    });
  };

  const handleFailedSnackbar = (message) => {
    setAlert({
      open: true,
      message: message,
      backgroundColor: "#FF3232",
    });
    //setBecomePartnerOpen(true);
  };

  const handleNewEventDialogForm = () => {
    setAddEvent(false);
  };

  const handleUpdatedEventDialogForm = () => {
    setUpdateEvent(false);
  };

  // const { token, setToken } = useToken();
  // const { userId, setUserId } = useUserId();
  const [expanded, setExpanded] = useState(false);
  const [alert, setAlert] = useState({
    open: false,
    message: "",
    backgroundColor: "",
  });
  const theme = useTheme();
  const matchesMD = useMediaQuery(theme.breakpoints.down("md"));
  const matchesSM = useMediaQuery(theme.breakpoints.down("sm"));
  const matchesXS = useMediaQuery(theme.breakpoints.down("xs"));
  const matchesMDUp = useMediaQuery(theme.breakpoints.up("md"));

  let imageUrl = "";
  if (product) {
    imageUrl = `${baseURL}/images/courses/${product.imageCover}`;
  }

  const renderNewEventFormForm = () => {
    return (
      <Dialog
        fullScreen={matchesXS}
        open={addEvent}
        onClose={() => [setAddEvent(false), history.push("/events")]}
      >
        <DialogContent>
          {/* <AddEventForm
            token={props.token}
            userId={props.userId}
            updateEventInfoHandler={props.updateEventInfoHandler}
            handleNewEventDialogForm={handleNewEventDialogForm}
            handleSuccessfulCreateSnackbar={handleSuccessfulCreateSnackbar}
            handleFailedSnackbar={handleFailedSnackbar}
            // user={user}
          /> */}
        </DialogContent>
      </Dialog>
    );
  };

  const renderUpdateEventFormForm = () => {
    return (
      <Dialog
        fullScreen={matchesXS}
        open={updateEvent}
        onClose={() => [setUpdateEvent(false), history.push("/events")]}
      >
        <DialogContent>
          {/* <UpdateEventForm
            token={props.token}
            userId={props.userId}
            updateEventInfoHandler={props.updateEventInfoHandler}
            handleUpdatedEventDialogForm={handleUpdatedEventDialogForm}
            handleSuccessfulCreateSnackbar={handleSuccessfulCreateSnackbar}
            handleFailedSnackbar={handleFailedSnackbar}
            //user={user}
          /> */}
        </DialogContent>
      </Dialog>
    );
  };

  const buttonContent = () => {
    return <React.Fragment>Learn More...</React.Fragment>;
  };

  const Str = require("@supercharge/strings");

  return (
    <>
      {matchesMDUp ? (
        <Card className={classes.root} disableRipple={true}>
          {/* <CardActionArea disableRipple> */}
          <Grid container direction="row">
            <Grid item style={{ width: "48%", border: "1px dotted grey" }}>
              <CardContent disableRipple>
                <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    Welcome to DNinjas, where limitless possibilities meet
                    extraordinary success! We are a dynamic and results-driven
                    digital marketing agency committed to propelling your
                    business to new heights in the online realm. Our passion
                    lies in harnessing the power of digital marketing strategies
                    to unlock your business potential and deliver remarkable
                    outcomes.
                  </ReactMarkdown>
                </Typography>
                <br />
                {/* <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    In today's fast-paced and ever-evolving digital landscape,
                    it takes more than just a website or a social media presence
                    to stand out from the crowd. It requires a comprehensive and
                    innovative approach that combines creativity, data-driven
                    insights, and cutting-edge technologies. That's where we
                    come in.
                  </ReactMarkdown>
                </Typography> */}
              </CardContent>
            </Grid>

            <Grid
              item
              style={{
                width: "50%",
                marginLeft: "1.7%",
                border: "1px dotted grey",
              }}
            >
              <CardContent disableRipple>
                <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    In today's fast-paced and ever-evolving digital landscape,
                    it takes more than just a website or a social media presence
                    to stand out from the crowd. It requires a comprehensive and
                    innovative approach that combines creativity, data-driven
                    insights, and cutting-edge technologies. That's where we
                    come in.
                  </ReactMarkdown>
                </Typography>
                <Button
                  component={Link}
                  // to="/mobileapps"
                  to={`/aboutus`}
                  varaint="outlined"
                  className={classes.submitButton}
                  onClick={() => <AboutUs />}
                  style={{ marginBottom: 10 }}
                >
                  {loading ? (
                    <CircularProgress size={30} color="inherit" />
                  ) : (
                    buttonContent()
                  )}
                </Button>
              </CardContent>
            </Grid>
          </Grid>
          {/* </CardActionArea> */}
        </Card>
      ) : (
        <Card className={classes.rootMobile} disableRipple>
          {/* <CardActionArea disableRipple> */}
          <Grid container direction="column">
            <Grid item style={{ width: "100%", border: "1px dotted grey" }}>
              <CardContent disableRipple>
                <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    Association of Old students of Adeolu Secondary School
                    (Assosa) are a group of people that attended Adeolu
                    Secondary School, Tolu School Complex, Olodi Apapa, Lagos
                    who are bound together by same purpose, mission and vision
                    to make a positive impacts on our alma mata, our community
                    and most especially on our members
                  </ReactMarkdown>
                </Typography>
                <br />
                <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    Our Vision is to promote a platform for collaboration,
                    networking, engagement and outreach among old students of
                    adeolu secondary school
                  </ReactMarkdown>
                </Typography>
              </CardContent>
            </Grid>

            <Grid
              item
              style={{
                width: "100%",
                marginLeft: "0%",
                marginTop: 10,
                border: "1px dotted grey",
              }}
            >
              <CardContent disableRipple>
                <Typography variant="h5" color="textSecondary" component="p">
                  <ReactMarkdown>
                    Our Mission is to harness our collective strength and
                    uniqueness to elevate our Alma mater, encourage and assist
                    current students , care for one another as members and
                    impact the society at large
                  </ReactMarkdown>
                </Typography>
              </CardContent>
            </Grid>
          </Grid>
          {/* </CardActionArea> */}
        </Card>
      )}
      <Dialog
        //style={{ zIndex: 1302 }}
        fullScreen={matchesXS}
        open={open}
        onClose={() => setOpen(false)}
        PaperProps={{
          style: {
            paddingTop: matchesXS ? "1em" : "3em",
            marginTop: 110,
            height: 540,
            paddingBottom: "3em",
            paddingLeft: matchesXS
              ? 0
              : matchesSM
              ? "3em"
              : matchesMD
              ? "10em"
              : "2em",
            paddingRight: matchesXS
              ? 0
              : matchesSM
              ? "5em"
              : matchesMD
              ? "10em"
              : "2em",
          },
        }}
      >
        <DialogContent>
          <Card className={classes.dialog}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                component="img"
                alt={product.name}
                image={imageUrl}
                crossOrigin="anonymous"
              />
            </CardActionArea>
          </Card>
        </DialogContent>
      </Dialog>
      {renderNewEventFormForm()}
      {renderUpdateEventFormForm()}

      <Snackbar
        open={alert.open}
        message={alert.message}
        ContentProps={{
          style: { backgroundColor: alert.backgroundColor },
        }}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => setAlert({ ...alert, open: false })}
        autoHideDuration={4000}
      />
    </>
  );
}
